import React, { Component } from 'react'
import Contact from './Contact'
import axios from 'axios'
import '../../bootstrap/css/bootstrap.css';
import { Container } from "./ListeContactStyle";
class ListesContacts extends Component{
    constructor(){
        super();
        this.handleDelete = this.handleDelete.bind(this);
        this.handlePut = this.handlePut.bind(this);
        this.handleDetails = this.handleDetails.bind(this);
        this.state = {isLoading: true, contacts:[],error:null, id:0, boom_1:false, boom_2:false}
    }
    componentDidMount(){
        axios.get('http://localhost:3333/learner')
        .then(res => {
            const contacts = res.data;
            this.setState({contacts, isLoading:false});
        })
        .catch(error => this.setState({ error, isLoading: false}))
    }
    handleAdd = (nom, prenom, numeroTelephone, addressEmail ) => {
        axios.post('http://localhost:3333/learner',{nom, prenom, numeroTelephone, addressEmail})
        .then(res => {
            this.setState({contacts: [...this.state.contacts, res.data]});
        })
    }
    render(){
        const learners = this.state.contacts;
        return (
            <Container>
            <React.Fragment>
                <div className = "container">
                <br/>
                <br/>
                    <div className="container border border-success solid rounded bg-dark-gray col-12">
                        <span>
                            <h1 className ="text-center">Liste des Contacts</h1>
                        </span>
                    </div>

                    <br/>
                    <br/>
                
                <div className ="card-body col-12">
                    <div className = "container">
                        {
                            learners.map(
                                learners => <Contact key={learners.id} learners={learners}
                                handleDelete={this.handleDelete}
                                handlePut={this.handlePut}
                                handleDetails={this.handleDetails}
                                boom_1={this.state.boom_1}
                                boom_2={this.state.boom_2}
                                id={this.state.id}
                                contact={learners}/>
                            )
                        }
                    </div>
                </div>
                </div>
            </React.Fragment>
            </Container>
            
    )
}
handleDelete(id){
    axios.delete('http://localhost:3333/learner/'+ id)
    .then(res => this.setState(prevState => ({
        contacts: prevState.contacts.filter(contact => contact.id !== id)
    })))
    .catch(error => this.setState({error, isLoading:false}))
    window.confirm("Voulez-vous supprimer ce contact ?");
}
handlePut(id){
    this.setState({boom_1:true, boom_2:false, id:id});
}
handleDetails(id){
    this.setState({boom_1:false, boom_2:true, id:id});
}
}
export default ListesContacts