import './App.css';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Header from './components/layout/Header'
import Footer from './components/layout/Footer'
import Accueil from './components/Pages/Accueil'
import AjouterContacts from './components/Pages/AjouterContacts'
import ListesContacts from './components/Pages/ListesContacts'
function App() {
  return (
    <div className="App"> 
        <BrowserRouter>
        <Header />
           <Switch>
             <Route exact path= "/" component={Accueil}/>
             <Route exact path="/listeContact" component={ListesContacts} /> 
             <Route exact  path="/ajouterContact" component={AjouterContacts} />
           </Switch>
        </BrowserRouter>
        
        <di><Footer /></di>
  
    </div>
    );
  
}

export default App;
