import { Component } from 'react'

class AddContact extends Component {
    constructor(){
        super();
        this.state={nom:'', prenom:'', numeroTelephone:'', addressEmail:''}
    }
    handleChange1 = event1 => this.setState({nom:event1.target.value})
    handleChange2 = event2 => this.setState({prenom:event2.target.value})
    handleChange3 = event3 => this.setState({numeroTelephone:event3.target.value})
    handleChange4 = event4 => this.setState({addressEmail:event4.target.value})
    handleAdd = e => {
        if(this.state.nom === "" || this.state.prenom === "" || this.state.numeroTelephone === "" || this.state.addressEmail === ""  ){
            alert("Veuillez remplir tous les champs");
        }else{
            e.preventDefault();
            this.props.handleAdd(this.state.nom, this.state.prenom, this.state.numeroTelephone, this.state.addressEmail);
            this.setState({nom:'', prenom:'', numeroTelephone:'', addressEmail:''});
            alert("Contact ajouter avec succss...");
        }
    }
    render(){
        return (
         <div className = "wrapper">
                <br/>
                <br/>
                <div className="container border border-success solid rounded bg-dark-gray col-12">
                    <span>
                        <h1 className= "text-center">Ajouter votre Contact</h1>
                    </span> 
               </div>
                
               <br/>
               <br/>
               <div className="wrapper">
                    <form onSubmit={this.handleAdd} className="col-lg-12">
                        <div className = "form-group">
                            <label for="exampletext1">Nom du Contact</label>
                            <input type="text" className="form-control" name = "nom"
                                placeholder= "nom"
                                value = {this.state.nom}
                                onChange = {this.handleChange1} id= "exampletext1"/>
                        </div>

                        <div className = "form-group">
                            <label for="exampletext2">Prenom du Contact</label>
                            <input type="text" className="form-control" name = "prenom"
                                placeholder= "prenom"
                                value = {this.state.prenom}
                                onChange = {this.handleChange2} id= "exampletext2"/>
                        </div>

                        <div className = "form-group">
                        <label for="exampletext3">Numero du Telephone</label>
                        <input type="text" className="form-control" name = "numeroTelephone"
                            placeholder= "Numero Telephone"
                            value = {this.state.numeroTelephone}
                            onChange = {this.handleChange3} id= "exampletext3"/>
                    </div>

                    <div className = "form-group">
                            <label for="exampletext4">Addresse Email</label>
                            <input type="text" className="form-control" name = "addressEmail"
                                placeholder= "Addresse Email"
                                value = {this.state.addressEmail}
                                onChange = {this.handleChange4} id= "exampletext4"/>
                        </div>
                        <button type="submit" className= "btn btn-success btn-lg" value = "Ajouter"
                        onClick={this.alert}>Ajouter Contact</button>
                    </form>
               </div>
                <br/>
                <br/>
           </div>
        )
    }
   
}
export default AddContact