import React from 'react'
import PutContact from './PutContact'
import axios from 'axios'
import '../../bootstrap/css/bootstrap.css';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

function Contact({boom_1,boom_2,id,contact,learners,handleDelete,handlePut,handleDetails}) {
    const handlePuts = (nom, prenom, numeroTelephone, addressEmail) => {
        axios.put('http://localhost:3333/learner/'+id,{nom, prenom, numeroTelephone, addressEmail})
    }
    var boom = '';
    if(id===contact.id && boom_1)
    {boom = <PutContact contact = {contact} handlePut ={handlePuts} />
        }else{
            if(id===contact.id && boom_2){
                boom = <div>
                            <p><strong>Nom: </strong> {learners.nom}</p>
                            <p><strong>Prenom: </strong> {learners.prenom}</p>
                            <p><strong>Numero de Telephone: </strong> {learners.numeroTelephone}</p>
                            <p><strong>Addresse EMail: </strong> {learners.addressEmail}</p>
                      </div>
            }
        }
    return (
        <Container fluid>
        <br/>
        <br/>
            <Row className = "container border border-success solid rounded">
            <br/>
            <br/>
                <Col><h5><small className= "text-muted">Nom <br/><br/>  </small> {learners.nom}</h5></Col>
                <Col><h5><small className= "text-muted">Prenom <br/><br/>   </small> {learners.prenom}</h5></Col>
                <Col><h5><small className= "text-muted">Numero Telephone <br/> <br/>  </small> {learners.numeroTelephone}</h5></Col>
                <Col><h5><small className= "text-muted">Adrresse Email <br/> <br/>   </small> {learners.addressEmail}</h5></Col>
                
                <br/>
                <br/>
                
            </Row>
           
            <br/>
            <br/>
       
            <Col><button type= "button" className= "btn btn-primary mr-2" 
            onClick={handleDetails.bind(this,learners.id)}>Dtails</button>
            
            <button type= "button" className= "btn btn-warning mr-2" 
            onClick={handlePut.bind(this,learners.id)}>Modifier</button>
           
            <button type= "button" className= "btn btn-danger mr-2" 
            onClick={handleDelete.bind(this,learners.id)}>
            <span className = "glyphicon glyphicon-remove">Supprimer</span></button>
            </Col>
            <br/>
            <br/>
           
        {boom}
        </Container>
        
    )
}
export default Contact
