import React, { Component } from 'react'
import axios from 'axios';
import AddContact from './AddContact'
import '../../bootstrap/css/bootstrap.css';
import { Container } from "./ajouterContactStyle";
    
class AjouterContacts extends Component {
    constructor(){
        super();
        this.state={isLoading:true, contacts:[], error:null}
    }
  
    render(){
        
        return (
            <Container>
            <React.Fragment>
                <div class = "container">
                    <AddContact handleAdd = {this.handleAdd} />
                </div>
            </React.Fragment>
            </Container>
        )
       
    }
    handleAdd = (nom, prenom, numeroTelephone, addressEmail ) => {
        axios.post('http://localhost:3333/learner',{nom, prenom, numeroTelephone, addressEmail})
        .then(res => {
            this.setState({contacts: [...this.state.contacts, res.data]});
        })
    }
  
}
export default AjouterContacts
