import  Carousel  from 'react-bootstrap/Carousel'
import React from 'react'
import Slide10 from '../images/Slide10.jpg'
import Image2 from '../images/Image2.png'
import fille from '../images/fille.jpg'
export default function Accueil() {
    return (
        <div>
            <React.Fragment>
                <Carousel>
                    <Carousel.Item>
                        <img className = "d-block w-100" src={Slide10} alt= "First slide"/>
                        <Carousel.Caption>
                            <h1>Welcome to Contact Manager</h1>
                            <p>Your best site for contact Manager</p>
                        </Carousel.Caption>
                    </Carousel.Item>

                    <Carousel.Item>
                        <img className = "d-block w-100" src={Image2} alt= "First slide"/>
                        <Carousel.Caption>
                            <h1>Welcome to Contact Manager</h1>
                            <p>Your best site for contact Manager</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    
                    <Carousel.Item>
                        <img className = "d-block w-100" src={fille} alt= "First slide"/>
                        <Carousel.Caption>
                            <h1>Welcome to Contact Manager</h1>
                            <p>Your best site for contact Manager</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
            </React.Fragment>
        </div>
    )
}
