import { Component } from 'react'
import '../../bootstrap/css/bootstrap.css';
class PutContact extends Component{
    constructor(props){
        super(props);
        this.state = {nom:this.contact().nom, prenom:this.contact().prenom, 
                    numeroTelephone:this.contact().numeroTelephone, addressEmail:this.contact().addressEmail}
    }
    handleChange1 = event1 => this.setState({nom:event1.target.value})
    handleChange2 = event2 => this.setState({prenom:event2.target.value})
    handleChange3 = event3 => this.setState({numeroTelephone:event3.target.value})
    handleChange4 = event4 => this.setState({addressEmail:event4.target.value})
    contact(){return this.props.contact;}
    render(){
        const contact = this.props.contact;
    return (
        <div>
            <form onSubmit={this.handlePut} className="col-lg-12">
                <div className="form-group">
                    <label for = "exampletext1">Nom du Contact</label>
                    <input type = "text" defaultValue={contact.nom} className="form-control" name="nom"
                    placeholder="nom"
                    onChange = {this.handleChange1} id="exampletext1"/>
                </div>

                <div className="form-group">
                    <label for = "exampletext2">Prenom du Contact</label>
                    <input type = "text" defaultValue={contact.prenom} className="form-control" name="prenom"
                    placeholder="prenom"
                    onChange = {this.handleChange2} id="exampletext2"/>
                </div>

                <div className="form-group">
                    <label for = "exampletext3">Numero Telephone</label>
                    <input type = "text" defaultValue={contact.numeroTelephone} className="form-control" name="numeroTelephone"
                    placeholder="numeroTelephone"
                    onChange = {this.handleChange3} id="exampletext3"/>
                </div>

                <div className="form-group">
                    <label for = "exampletext4">Adresse Email</label>
                    <input type = "text" defaultValue={contact.addressEmail} className="form-control" name="addressEmail"
                    placeholder="addressEmail"
                    onChange = {this.handleChange4} id="exampletext4"/>
                </div>
                <button type ="submit" className="btn btn-success btn-lg" value="Ajouter">
                Modifier Contact</button>
                
            </form>
        </div> 
    )
}
handlePut = e =>{
    if(this.state.nom ==="" || this.state.prenom ==="" || this.state.numeroTelephone ==="" || this.state.addressEmail ==="" )
    {alert("Veuillez remplir tout les champs...");
}else{
    e.preventDefault();
    this.props.handlePut(this.state.nom, this.state.prenom, 
        this.state.numeroTelephone, this.state.addressEmail);
        window.confirm("Voulez-vous modifier ?");
        alert("Votre contact  t modifier avec succs...");
}
}
}
export default PutContact
